<template>
  <div>
    <div v-for="classInfo in classesColorScale" :key="classInfo.displayName">
      <template v-if="loadedElements.has(classInfo.element.toUpperCase())">
        <h3>{{ classInfo.displayName }}</h3>
        <div class="color-scale">
          <div v-for="colorInfo in classInfo.colorScale" :key="colorInfo.label" class="color-box">
            <div class="color-swatch" :style="{ backgroundColor: colorInfo.color }"></div>
            <div class="color-label">{{ colorInfo.label }}<br />{{ colorInfo.range }}</div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<style scoped>
.color-scale {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.color-box {
  width: 40px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.color-swatch {
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
  border: solid 1px #000;
}

.color-label {
  font-size: 8px;
  text-align: center;
  line-height: 10px;
}
</style>

<script setup lang="ts">
import { useColorScale } from '@stores/color-scale'
import { onMounted, onUnmounted, inject, ref } from 'vue'

const colorScaleStore = useColorScale()
const classesColorScale = colorScaleStore.classColors
console.debug(classesColorScale)

const emitter = inject('emitter')

onMounted(() => {
  emitter.on('lkp-show-in-map', showElement)
  emitter.on('lkp-hide-in-map', hideElement)
  emitter.on('lkp-interpolate-in-map', showElement)
})
onUnmounted(() => {
  emitter.off('lkp-show-in-map', showElement)
  emitter.off('lkp-hide-in-map', hideElement)
  emitter.off('lkp-interpolate-in-map', showElement)
})

const loadedElements = ref(new Set<string>())

function showElement(treeViewNode) {
  loadedElements.value.add(treeViewNode.data.legendElement.toUpperCase())
  console.debug('New legend display', loadedElements.value)
}
function hideElement(treeViewNode) {
  loadedElements.value.delete(treeViewNode.data.legendElement.toUpperCase())
  console.debug('New legend display', loadedElements.value)
}
</script>
